/**
 * @description (/)
 * @author FerdYing
 * @param {String}  timeString
 */
const changeDateFormat = (timeString) => {
  return timeString.substr(0, 4) + '/' + timeString.substr(4, 2) + '/' + timeString.substr(6, 2);
}

/**
 * @description 1970-1-1 00:00:00 UTC UTC
 * @author FerdYing
 * @param {String} timeString ["20170903"]
 */
const parseDate = timeString => {
  return new Date(Date.parse(changeDateFormat(timeString)));
}

/**
 * @description weekday
 * @author FerdYing
 * @param {Int}  day
 */
const weekDayFormat = (day) => {
  switch (day) {
    case 0:
      return ''
      break;
    case 1:
      return ''
      break;
    case 2:
      return ''
      break;
    case 3:
      return ''
      break;
    case 4:
      return ''
      break;
    case 5:
      return ''
      break;
    case 6:
      return ''
      break;
    default:
      break;
  }
}

/**
 * @description 
 * @author FerdYing
 * @param {String}  dateString
 */
export const dateFormat = (dateString) => {
  let date = parseDate(dateString);
  let mounth = date.getMonth() + 1;
  let day = date.getDate();
  let weekday = weekDayFormat(date.getDay());
  return mounth + '' + day + ' ' + '' + weekday;
}

/**
 * @description 
 * @author FerdYing
 * @param {number} date {1506395861}
 */

export const commentDateFormat = (date) => {
  let d = new Date(date * 1000)
  return d.getMonth() + 1 + '-' + d.getDate() + ' ' + d.getHours() + ':' + d.getMinutes()
}



